import React from "react";
import { TEXT_AREA_BOX_STYLES } from "assets/base-theme";
import { IInputCommonStyleProps } from "components/interface/Input";
import BaseInputTextArea, { IBaseInputTextAreaProps } from "components/base/BaseInputTextArea";
import { generateClasses } from "utils/Util";
import { useTranslation } from "react-i18next";

interface IInputTextAreaBoxProps extends IBaseInputTextAreaProps, IInputCommonStyleProps {
  maxCount?: number;
  hideMaxCount?: boolean;
  disableMaxCount?: boolean;
}
const InputTextArea: React.FC<IInputTextAreaBoxProps> = (props: IInputTextAreaBoxProps) => {
  const { variant, styleObj, ...BaseInputProps } = props;
  const {
    value,
    onChange,
    placeholder,
    label,
    validation,
    maxCount,
    hideMaxCount,
    disableMaxCount,
  } = BaseInputProps;
  const getClassNames = () => {
    const styleObject = {
      ...TEXT_AREA_BOX_STYLES,
      overrideStyle: styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: validation ? true : false,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, variant);
  };
  const { t } = useTranslation();

  const translatedPlaceholder = t(placeholder ?? "");

  return (
    <BaseInputTextArea
      {...BaseInputProps}
      className={getClassNames()}
      value={value}
      onChange={onChange}
      placeholder={translatedPlaceholder}
      label={label}
      validation={validation}
      maxCount={maxCount ?? 200}
      hideMaxCount={hideMaxCount}
      disableMaxCount={disableMaxCount}
    />
  );
};

// memoization

export default React.memo(InputTextArea);
// redux wiring
