import { InputTextarea, InputTextareaProps } from "primereact/inputtextarea";
import React from "react";
import { IBaseInputCommonProps } from "components/interface/Input";
import Label from "components/Label";
import InputError from "components/InputError";
import { withAuth } from "components/hoc/withAuth";

export interface IBaseInputTextAreaProps extends InputTextareaProps, IBaseInputCommonProps {
  maxCount?: number;
  hideMaxCount?: boolean;
  disableMaxCount?: boolean;
}
const BaseInputTextArea: React.FC<IBaseInputTextAreaProps> = (props: IBaseInputTextAreaProps) => {
  const {
    maxCount,
    hideLabel,
    hideMaxCount,
    disableMaxCount,
    label,
    labelClassNames,
    labelStyleObj,
    validation,
    hideErrorRow,
    disabled,
    isDisabledByAcl,
    errorClassNames,
    errorStyleObj,
    reportFormDirty,
    ...PrimeReactProps
  } = props;

  const { placeholder, className, value, name, onChange, rows } = PrimeReactProps;

  function getCount() {
    let currentCount = 0;
    if (maxCount && value) {
      currentCount = value.toString().length;
    }
    return currentCount;
  }

  function handleCount(e: any) {
    if (!disableMaxCount) {
      if (e && e.target && e.target.value && maxCount) {
        const val = e.target.value;
        if (val.length > maxCount) {
          e.target.value = val.substring(0, maxCount);
        }
      }
    }
    if (onChange) {
      onChange(e);
    }
  }

  const currentCount = getCount();
  return (
    <div className="flex flex-col h-full">
      {!hideLabel && (
        <Label
          label={label}
          labelClassNames={labelClassNames ? labelClassNames : " mb-1 "}
          labelStyleObj={labelStyleObj}
          isInvalid={validation}
        />
      )}
      <InputTextarea
        {...PrimeReactProps}
        className={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor border-primary-gray-300 placeholder-secondary-gray-500"
            : " "
        } `}
        value={value}
        onChange={handleCount}
        name={name}
        placeholder={placeholder}
        disabled={disabled || isDisabledByAcl}
        rows={rows}
      />
      {!hideMaxCount && maxCount ? (
        <div className="flex justify-between">
          <InputError validation={validation} hideErrorRow={hideErrorRow} errorClassNames="" />
          <div className="text-xs text-right text-secondary-gray-500">
            {currentCount} / {maxCount}
          </div>
        </div>
      ) : (
        <InputError validation={validation} hideErrorRow={hideErrorRow} errorClassNames="" />
      )}
      {/* <InputError validation={validation} hideErrorRow={hideErrorRow} errorClassNames="" /> */}
    </div>
  );
};

export default withAuth(BaseInputTextArea);
